.ImageGalleryItem_gallery-item__2ygkk {
  overflow: hidden;
  margin-left: 30px;
  margin-top: 30px;
  width: calc((100% - 4 * 30px) / 4);

  border-radius: 5px 5px;

  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);

  cursor: pointer;
}

.ImageGalleryItem_gallery-item__2ygkk:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

.ImageGalleryItem_gallery-img__2d9EF {
  display: block;
  width: 100%;
  height: 300px;
  transition: -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: -webkit-transform var(--animation-duration) var(--timing-function);
  transition: transform var(--animation-duration) var(--timing-function);
  transition: transform var(--animation-duration) var(--timing-function), -webkit-transform var(--animation-duration) var(--timing-function);
  object-fit: cover;
  object-position: top;
}

